.app-container{max-width:1200px;margin:0 auto}

.gallery-box{width:25%;min-height:330px}.gallery-box a{text-decoration:none;color:aliceblue}.gallery-box:hover{opacity:0.85}.gallery-box .picture-box{height:200px;background-size:cover;background-position:center}.gallery-box__info span{height:130px}.gallery-box__info .date{text-align:right;font-weight:100}.background-color{background-color:#606060}


.single-item{min-height:150px;display:flex;width:30%;flex-direction:column}#footer{margin-top:auto;background-color:blue;opacity:0.5}

.header{display:flex;justify-content:space-between;background-color:#606060}.header a{text-decoration:none;color:aliceblue}.header .logo-part{display:flex;padding:6px 20px;align-items:center;cursor:pointer}.header .logo-part h2{text-transform:uppercase;letter-spacing:3px}.header .buttons a{width:110px;display:flex;justify-content:center;align-items:center}.header .buttons a:hover{background-color:#29b6f6}.header .buttons .nav-button{height:100%;width:120px;font-size:18px;color:white}.header .user-panel-header{width:60px;height:60px;border-radius:50%;background-size:cover;background-position:center;align-self:center;cursor:pointer}@media (prefers-reduced-motion: no-preference){.logo{animation:App-logo-spin infinite 30s linear}}@keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}

.user-panel{width:200px;height:300px;border:1px solid aliceblue;position:absolute;background-color:aliceblue;color:black;right:130px;top:90px}

